.tooltip__container {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  box-sizing: border-box;
  max-width: 430px;
  padding: 60px 36px;
  border: none;
  border-radius: 10px;
  box-shadow: 0 0 25px 0 rgba(0, 0, 0, 0.5);
  background-color: #fff;
}


@media screen and (max-width:560px) {
  .tooltip__container {
    max-width: 280px;
    padding: 50px 18px;
  }
}